import React, {useEffect, useState} from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import { EventAPI } from "../../../commons/EventAPI";

const dispatchedEvents = [];

const SrsMuiAutocomplete = function (props, ref) {
  const [selectedValue, setSelectedValue] = useState(props.selected);

  const handleSelectedChange = (evt, changedValue) => {
    props.dispatchEvent(EventAPI.MUI_SELECT_CHANGED, { payload: { value: changedValue, id: props.elementid } });
    props.setValue(changedValue);
    setSelectedValue(changedValue);
  };

  useEffect(() => {
    if(selectedValue != props.selected) {
      setSelectedValue(props.selected);
    }
  }, [props.selected])

  let defaultRenderInput = (params) => <TextField {...params} label={props.label} inputProps={{...params.inputProps, autoComplete: 'new-password'}}/>
  let componentProps = {...props, theme: props.muiTheme};
  return (
    <Autocomplete {...componentProps} renderInput={defaultRenderInput} onChange={handleSelectedChange} value={selectedValue}>
      <slot />
    </Autocomplete>
  );
}

const styles = `
  /* Custom styles for the Autocomplete component */
`;

const props = [
  {
    name: "label",
    defaultValue: "",
    type: "string",
  },
  {
    name: "id",
    defaultValue: "",
    type: "string",
  },
  {
    name: "options",
    defaultValue: [],
    type: "object",
  },
  {
    name: "selected",
    defaultValue: null,
    type: "string",
  },
  {
    name: "className",
    defaultValue: "",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object",
  },
  // Add other props specific to Autocomplete if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(React.forwardRef(SrsMuiAutocomplete), props, "mui");
export { WrappedComponent, styles, props, dispatchedEvents, slots };
